























































@import './assets/stylus/mixin.styl'
  .tab
    display: flex
    width: 100%
    height: 0.8rem
    line-height: 0.8rem
    
    border-top:0.02rem solid rgba(7,17,27,.1)
    .tab-item
      flex: 1
      text-align: center
      & > a
        text-decoration :none
        display: block
        font-size: 0.28rem
        line-height:0.8rem
        color: rgb(77,85,93)
        &.active
          color: rgb(240,20,20)
