















































































































































@import '../../assets/stylus/mixin.styl'

.ratings
  position: absolute
  top: 3.52rem
  left: 0
  bottom: 0
  right: 0
  margin: 0 auto
  border-top:0.02rem solid rgba(7,17,27,0.1)
 
  overflow: hidden
  .rating-wrapper
    padding: 0.36rem 0
    display: flex
    .rating-left
      flex: 0 0 2.74rem
      width: 2.74rem
      padding: 0.12rem 0
      text-align: center
      border-right: 0.02rem solid rgba(7,17,27,0.1)
      .score
        font-size: 0.48rem
        line-height: 0.56rem
        color: rgb(255,153,0)
        margin-bottom: 0.12rem
      .title
        font-size: 0.24rem
        line-height: 0.24rem
        color: rgb(7,17,27)
        margin-bottom: 0.16rem
      .rank
        font-size: 0.2rem
        line-height: 0.2rem
        color: rgb(147,153,159)
    .rating-right
      flex: 1
      padding-left: 0.48rem
      .score-wrapper
        font-size: 0
        margin-bottom: 0.16rem
        line-height: 0.36rem
        .title
          display: inline-block
          font-size: 0.24rem
          color: rgb(7,17,27)
        .star
          margin-left: 0.24rem
          display: inline-block
        .score
          margin-left: 0.24rem
          font-size: 0.24rem
          color: rgb(255,153,0)
      .delivery-wrapper
         font-size: 0
         .title
           display: inline-block
           font-size: 0.24rem
           color: rgb(7,17,27)
         .time
           margin-left: 0.24rem
           display: inline-block
           font-size: 0.24rem
           color: rgb(147,153,159)
  .split
    height: 0.32rem
    border-top: 0.02rem solid rgba(7,17,27,0.1)
    border-bottom: 0.02rem solid rgba(7,17,27,0.1)
    background: #f3f5f7
  .ratingselect
    padding: 0.36rem 0.36rem 0.24rem 0.36rem
    border-bottom: 0.02rem solid rgba(7,17,27,.1)
  .rating-content
    padding: 0 0.36rem
    .rating-item
      position: relative
      display: flex
      padding: 0.36rem 0
      border-top:0.02rem solid rgba(7,17,27,.1)
      .avatar
        flex: 0 0 0.56rem
        width: 0.56rem
        margin-right: 0.24rem
        img
          border-radius: 50%
      .content-wrapper
        flex: 1
        .name
          font-size: 0.2rem
          line-height: 0.24rem
          color: rgb(7,17,27)
          margin-bottom: 0.08rem
        .star-wrapper
          font-size: 0
          margin-bottom: 0.12rem
          .star
            display: inline-block
            margin-right: 0.12rem
          .time
            font-size: 0.2rem
            font-weight: 200
            line-height: 0.24rem
            color: rgb(147,153,159)
        .text
          font-size: 0.24rem
          line-height: 0.36rem
          color: rgb(7,17,27)
          margin-bottom: 0.16rem
        .recommend
          font-size: 0
          .icon-thumb_up
            display: inline-block
            font-size: 0.24rem
            color: rgb(0,160,220)
            line-height: 0.32rem
          .recommend-item
            display: inline-block
            padding: 0 0.12rem
            font-size: 0.18rem
            line-height: 0.32rem
            color: rgb(147,153,159)
            border: 0.02rem solid rgba(7,17,27,.1)
            margin: 0 0 0.08rem 0.16rem
        .rate-time
          position: absolute
          right: 0.36rem
          top: 0.36rem
          font-size: 0.2rem
          line-height: 0.24rem
          color: rgb(147,153,159)


