













































































































































@import '../../assets/stylus/mixin.styl'

.goods
  position: absolute
  top: 3.52rem
  bottom: 0.88rem
  width: 100%
  border-top:0.02rem solid rgba(7,17,27,0.1)

  
  display: flex
  overflow: hidden
  .menu-wrapper
    flex: 0 0 1.6rem
    background: #f3f5f7
    .menu-item
      width: 1.12rem
      height: 1.08rem
      display: table
      font-size: 0.24rem
      line-height: 0.28rem
      padding: 0 0.24rem
      font-weight: 200
      &:first-child
        margin-top: -0.02rem
      &:last-child
        .text:after
          border-top: none
      &.current
        position: relative
        background: #fff
        font-weight: 700
        margin-top: -0.02rem
        z-index: 2
        
        .text:after
          border-top: 0
      .text
        display: table-cell
        vertical-align: middle
        border-top:0.02rem solid rgba(7,17,27,.1)
        width: 1.12rem
        font-size: 0.24rem
        .icon
          vertical-align: top
          display: inline-block
          width: 0.24rem
          height: 0.24rem
          margin-right: 0.08rem
          background-size: 0.24rem 0.24rem
          background-repeat: no-repeat
          &.decrease
            bg-image('decrease_3')
          &.discount
            bg-image('discount_3')
          &.guarantee
            bg-image('guarantee_3')
          &.invoice
            bg-image('invoice_3')
          &.special
            bg-image('special_3')
  .foods-wrapper
    flex: 1
    .title
      padding-left: 0.28rem
      border-left: 0.04rem solid #d9dde1
      font-size: 0.24rem
      height: 0.52rem
      line-height: 0.52rem
      color: rgb(147,153,159)
      background-color: #f3f5f7
    .food-item
      position:relative
      display: flex
      margin: 0.36rem
      padding-bottom: 0.36rem
      border-bottom:0.02rem solid rgba(7,17,27,.1)
      &:last-child:after
        border-top: 0
      .icon
        flex: 0 0 1.14rem
        margin-right: 0.20rem
      .content
        flex: 1
        .name
          margin: 0.04rem 0 0.16rem 0
          height: 0.28rem
          line-height: 0.28rem
          font-size: 0.28rem
          color: rgb(7,17,27)

        .description,.extra
          font-size: 0.20rem
          line-height: 0.20rem
          color: rgb(147,153,159)
          margin-bottom: 0.16rem
        .extra
            .count
              margin-right: 0.24rem
              line-height: 0.20rem
            .rating
              line-height: 0.20rem
        .price
          font-size:0
          line-height: 0
          padding: 0
          padding-top: 0.16rem
          .now
            font-size: 0.28rem
            color: rgb(240,20,20)
            font-weight: 700
            line-height: 0.48rem
            margin-right: 0.16rem
          .old
             font-size: 0.20rem
             text-decoration: line-through
             color: rgb(147,153,159)
             font-weight: 700
             line-height: 0.48rem
        .cart-wrapper
          position: absolute
          right: 0
          bottom: 0.24rem
