



























































































































































































































@import '../../assets/stylus/mixin'

.shopcart
  position: fixed
  
  left: 0
  right: 0
  margin: 0 auto
  bottom: 0
  width: 100%
  height: 0.96rem
  z-index: 4
  .content
    display: flex
    background: #141d27
    color: rgba(255,255,255,.4)
    .content-left
      flex: 1
      font-size: 0
      .logo-wrapper
        padding: 0.12rem
        margin: 0 0.24rem
        background: #141d27
        width: 1.12rem
        height: 1.12rem
        display: inline-block
        position: relative
        top: -0.20rem
        box-sizing: border-box
        border-radius: 50%
        .logo
          width: 100%
          height: 100%
          border-radius: 50%
          background: #2b343c
          text-align: center
          line-height: 0.88rem
          &.highlight
            background: rgb(0,160,220)
          .icon-shopping_cart
            font-size: 0.48rem
            line-height: 0.88rem
            color: #80858a
            &.highlight
              color: #fff
        .num
          position: absolute
          top: 0
          right: 0
          width: 0.48rem
          height: 0.32rem
          line-height: 0.32rem
          text-align: center
          border-radius: 0.32rem
          font-size: 0.18rem
          font-weight: 700
          color: #fff
          background: rgb(240,20,20)
          box-shadow: 0 0.08rem 0.16rem 0 rgba(0,0,0,.4)
      .price
        display: inline-block
        height: 0.48rem
        line-height: 0.48rem
        vertical-align: top
        margin-top: 0.24rem
        font-size: 0.32rem
        font-weight: 700
        border-right: 0.02rem solid rgba(255,255,255,.1)
        padding-right: 0.24rem
        &.highlight
          color: #fff
      .desc
        display: inline-block
        font-size: 0.2rem
        line-height: 0.48rem
        vertical-align: top
        margin: 0.24rem 0 0 0.24rem
    .content-right
      flex: 0 0 2.1rem
      width: 2.1rem
      .pay
        height: 0.96rem
        line-height: 0.96rem
        font-size: 0.24rem
        font-weight: 700
        text-align: center
        &.not-enough
          background: #2b333b
        &.enough
          background: #00b43c
          color: #fff
  .ball-container
    .ball
      position: fixed
      left: 0.64rem
      bottom: 0.44rem
      z-index: 200
      &.drop-enter, &.drop-enter-active
        transition all 0.4s cubic-bezier(0.49,-0.29,0.75,0.41)
      .inner
        display: inline-block
        width: 0.32rem
        height: 0.32rem
        border-radius: 50%
        background: rgb(0, 160, 220)
        transition: all .4s linear
  .shopcart-list
    position: absolute
    left: 0
    top: 0
    width: 100%
    z-index: -1
    transform: translate3d(0, -100%, 0)
    &.fold-enter-active, &.fold-leave-active
      transition: all 0.5s
    &.fold-enter, &.fold-leave-to
      transform: translate3d(0, 0, 0)
  .list-header
    height: 0.8rem
    line-height: 0.8rem
    padding: 0 0.32rem
    background: #f3f5f7
    border-bottom: 0.02rem solid rgba(7,17,27,.1)
    .title
      font-size: 0.28rem
      float: left
      color: rgb(7,17,27)
    .empty
      float: right
      font-size: 0.24rem
      color: rgb(0,160,220)
  .list-content
    padding: 0 0.32rem
    
    background: #fff
    overflow: hidden
    .food
      position: relative
      padding: 0.24rem 0
      box-sizing: border-box
      border-top:0.02rem solid rgba(7,17,27,.1)
      &:last-child:after
        border-top: 0
      .name
        line-height: 0.48rem
        font-size: 0.28rem
        color: rgb(7,17,27)
      .price
        position: absolute
        right: 1.8rem
        bottom: 0.24rem
        line-height: 0.48rem
        font-size: 0.28rem
        font-weight: 700
        color: rgb(240,20,20)
      .cartcontrol-wrapper
        position: absolute
        right: 0
        bottom: 0.12rem
  .list-mask
    position: fixed
  
    top: 0
    left: 0
    right: 0
    margin: 0 auto
    width: 100%
    height: 100%
    background: rgba(7,17,27,.6)
    backdrop-filter: blur(0.20rem)
    z-index: -2;
    opacity: 1
    &.mask-fade-enter-active, &.mask-fade-leave-active
      transition: all 0.3s
    &.mask-fade-enter, &.mask-fade-leave-to
      opacity: 0
