














































@import '../../assets/stylus/mixin'

  .star
    .star-item
      display: inline-block
      background-repeat: no-repeat
  &.star-48
    .star-item
      width: 0.4rem
      height: 0.4rem
      margin-right: 0.44rem
      background-size: 0.4rem 0.4rem
      &:last-child
        margin-right: 0
      &.on
        bg-image('star48_on')
      &.half
        bg-image('star48_half')
      &.off
        bg-image('star48_off')
  &.star-36
    .star-item
      width: 0.3rem
      height: 0.3rem
      margin-right: 0.12rem
      background-size: 0.3rem 0.3rem
      &:last-child
        margin-right: 0
      &.on
        bg-image('star36_on')
      &.half
        bg-image('star36_half')
      &.off
        bg-image('star36_off')
  &.star-24
    .star-item
      width: 0.2rem
      height: 0.2rem
      margin-right: 0.06rem
      background-size: 0.2rem 0.2rem
      &:last-child
        margin-right: 0
      &.on
        bg-image('star24_on')
      &.half
        bg-image('star24_half')
      &.off
        bg-image('star24_off')
    

