



















































































































































@import '../../assets/stylus/mixin.styl'

.food
  
  position: fixed
  width: 100%
  z-index: 2
  top: 0
  left: 0
  right: 0
  margin: 0 auto
  bottom: 0.96rem
  background: #fff
  &.bounce-enter-active
    animation: bounce-in .3s
  &.bounce-leave-active
    animation: bounce-in .3s reverse
  @keyframes bounce-in
    0%
      transform: scale(0)
    100%
      transform: scale(1)
  .food-content
    .image-header
      position: relative
      width: 100%
      height: 0
      padding-bottom: 100%
      img
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
      .back
        position: absolute
        top: 0.24rem
        left: 0
        .icon-close
          display: inline-block
          padding: 0.20rem
          color: #fff
          font-size: 0.4rem
    .content
      position: relative
      padding: 0.36rem
      .title
        font-size: 0.28rem
        line-height: 0.28rem
        font-weight: 700
        color: rgb(7,17,27)
        margin-bottom: 0.16rem
      .detail
        font-size: 0.20rem
        line-height: 0.20rem
        color: rgb(147,153,159)
        margin-bottom: 0.36rem
        .sell-count
          margin-right: 0.24rem
      .price
        font-size: 0.28rem
        font-weight: 700
        line-height: 0.48rem
        color: rgb(240,20,20)
        .old
          font-size: 0.20rem
          font-weight: 700
          line-height: 0.48rem
          color: rgb(147,143,159)
          text-decoration: line-through
          margin-left: 0.20rem
      .cartcontrol-wrapper
        position: absolute
        right: 0.24rem
        bottom: 0.24rem
      .buy
        position: absolute
        right: 0.36rem
        bottom: 0.36rem
        padding: 0 0.24rem
        height: 0.48rem
        line-height: 0.48rem
        background: rgb(0,160,220)
        font-size: 0.20rem
        color: rgb(255,255,255)
        border-radius: 0.24rem
        opacity: 1
        &.fade-enter-active, &.fade-leave-active
           transition: opacity .5s
        &.fade-enter, &.fade-leave-to /* .fade-leave-active in below version 2.1.8 */
           opacity: 0
    .split
      height: 0.32rem
      border-top: 0.02rem solid rgba(7,17,27,0.1)
      border-bottom: 0.02rem solid rgba(7,17,27,0.1)
      background: #f3f5f7
    .info
      padding: 0.36rem
      .title
        font-size: 0.28rem
        line-height: 0.28rem
        margin-bottom: 0.12rem
        color: rgb(7,17,27)
        font-weight: 500
      .text
        padding: 0 0.16rem
        font-size: 0.24rem
        font-weight: 200
        color: rgb(77,85,93)
        line-height: 0.48rem
    .rating
      padding: 0.36rem 0.36rem 0.24rem 0.36rem
      border-bottom:0.02rem solid rgba(7,17,27,.1)
      .title
        font-size: 0.28rem
        line-height: 0.28rem
        margin-bottom: 0.12rem
        color: rgb(7,17,27)
        font-weight: 500
    .rating-wrapper
      padding: 0 0.36rem
      .rating-item
        padding: 0.32rem 0
        border-top:0.02rem solid rgba(7,17,27,.1)
        .user
          position: absolute
          right: 0
          top: 0.32rem
          font-size: 0
          .name
            display: inline-block
            margin-right: 0.12rem
            font-size: 0.20rem
            line-height: 0.24rem
            color: rgb(147,153,159)
            vertical-align: top
          .avatar
            border-radius: 50%
            vertical-align: top
        .time
          font-size: 0.20rem
          color: rgb(147,153,159)
          line-height: 0.24rem
        .text
          margin-top: 0.12rem
          font-size: 0
          .icon-thumb_down
            display: inline-block
            margin-right: 0.08rem
            font-size: 0.24rem
            color: rgb(147,153,159)
            line-height: 0.32rem
            vertical-align: top
          .icon-thumb_up
            display: inline-block
            margin-right: 0.08rem
            font-size: 0.24rem
            color: rgb(0,160,220)
            line-height: 0.32rem
            vertical-align: top
          .text-content
            font-size: 0.24rem
            color: rgb(7,17,27)
            line-height: 0.32rem
      .no-rating
        padding: 0.24rem 0
        font-size: 0.24rem
        color: rgb(147,153,159)

