






































































































































@import '../../assets/stylus/mixin.styl'

.seller
  position: absolute
  top: 3.52rem
  left: 0
  right: 0
  bottom: 0
  
  margin: 0 auto
  overflow: hidden
  .seller-content
    .overview
      border-top:0.02rem solid rgba(7,17,27,0.1)
      padding: 0.36rem
      position: relative
      .title
        font-size: 0.28rem
        line-height: 0.28rem
        color: rgb(7,17,27)
        margin-bottom: 0.16rem
      .desc
        font-size: 0
        padding-bottom: 0.36rem
        margin-bottom: 0.36rem
        border-bottom:0.02rem solid rgba(7,17,27,.1)
        .star
          display: inline-block
          margin-right: 0.16rem
          vertical-align: top
        .text
          vertical-align: top
          display: inline-block
          font-size: 0.2rem
          color: rgb(77,85,93)
          line-height: 0.36rem
          margin-right: 0.24rem
      .remark
        display: flex
        .block
          flex: 1
          text-align: center
          border-right: 0.02rem solid rgba(7,17,27,.1)
          &:last-child
            border-right: none
          .title
            font-size: 0.2rem
            line-height: 0.2rem
            color: rgb(147,153,159)
            margin-bottom: 0.08rem
          .content
            font-size: 0.2rem
            color: rgb(7,17,27)
            .stress
              font-size: 0.48rem
              line-height: 0.48rem
      .favorite
        position: absolute
        width: 1rem
        right: 0.24rem
        top: 0.36rem
        text-align: center
        font-size: 0
        .icon-favorite
          display: inline-block
          color: #d4d6d9
          font-size: 0.48rem
          line-height: 0.48rem
          margin-bottom: 0.08rem
          &.active
            color: rgb(240,20,20)
        .text
          font-size: 0.2rem
          line-height: 0.2rem
          color: rgb(77,85,93)
    .split
      width: 100%
      height: 0.32rem
      border-top: 0.02rem solid rgba(7,17,27,0.1)
      border-bottom: 0.02rem solid rgba(7,17,27,0.1)
      background: #f3f5f7
    .bulletin
      padding: 0.36rem 0.36rem 0 0.36rem
      .title
        font-size: 0.28rem
        line-height: 0.28rem
        color: rgb(7,17,27)
        margin-bottom: 0.16rem
      .content-wrapper
        padding: 0 0.24rem 0.32rem 0.24rem
        
        .content
          font-size: 0.24rem
          font-weight: 200
          color: rgb(240,20,20)
          line-height: 0.48rem
      .supports
        .support-item
          padding: 0.32rem 0.24rem
          font-size: 0
          border-top:0.02rem solid rgba(7,17,27,.1)
          &:last-child:after
            border-top: none
          .icon
            vertical-align: top
            display: inline-block
            width: 0.32rem
            height: 0.32rem
            margin-right: 0.12rem
            background-size: 0.32rem 0.32rem
            background-repeat: no-repeat
            &.decrease
              bg-image('decrease_4')
            &.discount
              bg-image('discount_4')
            &.guarantee
              bg-image('guarantee_4')
            &.invoice
              bg-image('invoice_4')
            &.special
              bg-image('special_4')
          .text
            font-size: 0.24rem
            line-height: 0.32rem
            color: rgb(7,17,27)
            font-weight: 200
    .pic
      padding: 0.36rem
      .title
        font-size: 0.28rem
        line-height: 0.28rem
        color: rgb(7,17,27)
        margin-bottom: 0.24rem
      .pic-wrapper
        white-space: nowrap
        font-size: 0
        .pic-item
          width: 2.4rem
          height: 1.8rem
          display: inline-block
          margin-right: 0.12rem
          &:last-child
            margin-right: 0
    .info
      padding: 0.36rem 0.36rem 0 0.36rem
      .title
        font-size: 0.28rem
        line-height: 0.28rem
        color: rgb(7,17,27)
        padding-bottom: 0.24rem
       
      .info-content
        .info-item
          padding: 0.32rem 0.24rem
          font-size: 0.24rem
          line-height: 0.32rem
          color: rgb(7,17,27)
          font-weight: 200
          border-top:0.02rem solid rgba(7,17,27,.1)
          &:last-child:after
            border-top: none
